import { CqsChunkStory, CqsClusterSummaryStory, CqsStory } from "@rp/common/api-types"

const getRelevantSentenceId = (story: CqsStory | CqsClusterSummaryStory) => {
  const [{ pnum, snum }] = story.sentences
  return `${story.id}-${pnum}-${snum}`
}

const getRelevantChunkId = (story: CqsChunkStory) => {
  const [{ cnum }] = story.chunks
  return `${story.id}-${cnum}`
}

export { getRelevantSentenceId, getRelevantChunkId }
